import * as React from 'react';

import { bin, max } from 'd3-array';

import { AxisBottom, AxisLeft } from '@visx/axis';
import { ParentSize } from '@visx/responsive';
import { scaleLinear } from '@visx/scale';

import values from './values@1.json';

export type Props = {
  width: number;
  height: number;
};

const COLOR = 'steelblue';

const Content = React.memo((props: Props) => {
  // eslint-disable-next-line
  const { width, height } = props;
  const margin = { top: 20, right: 20, bottom: 30, left: 40 };
  const bins = React.useMemo(() => bin().thresholds(40)(values), [values]);
  const [x, y] = React.useMemo(() => {
    const xScale = scaleLinear({
      domain: [bins[0].x0 || 0, bins[bins.length - 1].x1 || 0],
      range: [margin.left, width - margin.right],
    });
    const yScale = scaleLinear({
      domain: [0, max(bins, (d) => d.length) || 0],
      range: [height - margin.bottom, margin.top],
      nice: true,
    });
    return [xScale, yScale];
  }, [bins, width, height]);

  // console.log('Content', { width, height, values, bins });
  return (
    <>
      <svg width={width} height={height}>
        <AxisLeft scale={y} left={margin.left} />
        <AxisBottom scale={x} top={height - margin.bottom} />
        {bins.map(({ x0 = 0, x1 = 0, length }, i) => (
          <rect key={i} x={x(x0)} y={y(length)} width={x(x1) - x(x0)} height={y(0) - y(length)} fill={COLOR} />
        ))}
      </svg>
    </>
  );
});

const BarChart = React.memo(() => <ParentSize>{({ width }) => <Content width={width} height={300} />}</ParentSize>);

export default BarChart;
